<template>
  <div>
    <div class="containerBox">
      <!-- 页面标题 -->
      <div class="pageTopic">
        <span class="topicName">全部病例</span>
        <router-link to="/case/save">
          <el-button type="primary" icon="el-icon-plus">添加病例</el-button>
        </router-link>
      </div>
      <!-- 商品列表 -->
      <div class="list">
        <el-table :data="caseListData" style="width: 100%" >
          <el-table-column prop="name" label="姓名" show-overflow-tooltip></el-table-column>
          <el-table-column prop="phoneNo" label="手机号" show-overflow-tooltip></el-table-column>
          <el-table-column prop="age" label="年龄"></el-table-column>
          <el-table-column prop="sex" label="性别">
            <template slot-scope="scope">
              <div> {{ scope.row.sex | transSex }} </div>
            </template>
          </el-table-column>
          <el-table-column prop="height" label="身高(cm)"></el-table-column>
          <el-table-column prop="weight" label="体重(kg)"></el-table-column>
          <el-table-column prop="startDate" label="就诊日期"></el-table-column>
          <el-table-column prop="treatmentNum" label="治疗次数"></el-table-column>
          <el-table-column label="操作" width="140">
            <template slot-scope="scope">
              <div>
                <el-link size="mini" type="primary" @click="gotoTreatment(scope.row.caseId)">设置治疗方案</el-link><br/>
                <el-link size="mini" type="primary" @click="gotoTreatmentRecord(scope.row.caseId)">治疗记录</el-link><br/>
                <el-link size="mini" type="primary" @click="gotoEdit(scope.row.caseId)">编辑个人信息</el-link><br/>
                <el-link size="mini" type="primary" @click="leaveHsp(scope.row.goodsId)">出院</el-link>
              </div>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!-- 分页 -->
      <div class="paging">
        <el-pagination @current-change="changePage"
                       :current-page="paginationData.page"
                       :hide-on-single-page="paginationData.size/paginationData.pageSize <= 1"
                       :page-size="paginationData.pageSize"
                       layout="total, prev, pager, next"
                       :total="paginationData.size">
        </el-pagination>
      </div>
    </div>
  </div>
</template>
<script>
import AppServerInf from "@/common/serverinf"

export default {
  data() {
    return {
      paginationData: {
        page: 1,
        pageSize: 10,
        size: 0
      },

      caseListData: [],
    }
  },
  created() {
    this.initCaseList(1)
  },
  methods: {
    initCaseList(page) {
      this.caseListData = this.$db.caseList()
    },
    changePage(val) {
      this.initCaseList(val)
    },
    gotoEdit(caseId) {
      this.$router.push(`/case/${caseId}/save`)
    },
    gotoTreatment(caseId) {
      this.$router.push(`case/${caseId}/treatment/save`)
    },
    gotoTreatmentRecord(caseId) {
      this.$router.push(`/case/${caseId}/treatment-record`)
    },
    leaveHsp(caseId) {
      this.$confirm('是否设置该用户出院?', '提示',{
      }).then(()=>{
        this.$db.deleteCase(caseId)
        this.caseListData = this.$db.caseList()
      })
    }
  },
  filters: {
    transSex(sexType) {
      return ['', '男', '女'][sexType]
    }
  }
}
</script>
<style>
</style>



